.links {
    margin-right: 30px;

    &__list {
        list-style: none;
        display: flex;
        gap: 15px;
        padding: 0;
        margin: 0;
        margin-top: 20px;
        margin-left: 10px;
    }
}

@include vp-1023 {
    .links {
        margin: 0;
        display: flex;
        justify-content: space-between;
        

        &__list {
            margin: auto 0;
        }
    }
}

@include vp-769 {
    .links {        
        display: flex;
        flex-direction: column;

        &__list {
            justify-content: center;
            margin-bottom: 20px;
        }

        &__logo {
            margin-left: auto;
            margin-right: auto;
        }
    }
}