.footer {
    background: $color-black;

    &__wrapper {
        max-width: 1272px;
        padding: 0 25px;
        margin: 0 auto;

        display: flex;
        color: #ffffff;
        padding-top: 50px;
        padding-bottom: 55px;
        justify-content: space-between;
    }

    &__header {
        font-family: 'Roboto Condensed', 'Arial', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 16px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: #ffffff;     
        margin: 0;
        margin-top: 5px;   
        margin-bottom: 20px;
    }

    &__list {
        list-style: none;
        padding: 0;
    }

    &__item {
        font-family: 'PT Mono', 'Rockwell', serif;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 135%;
        color: rgba(255, 255, 255, 0.7);
    }
}

@include vp-1023 {
    .footer {
        padding: 0;
        padding-top: 50px;
        padding-bottom: 65px;

        &__wrapper {
            max-width: none;
            display: grid;
            grid-template-columns: 1fr 1fr;
        }

        &__section--links {
            grid-column: 1/-1;
        }

        &__header {
            margin-top: 35px;
        }
    }
}

@include vp-769 {
    .footer {
        padding: 35px 0;

        &__wrapper{
            display: flex;
            flex-direction: column;   
            padding: 0;
        }  

        &__header{
            margin: 0;
        }

        &__section {
            margin: 0;
        }
    }
}