.about {
    margin: 0 auto 120px auto;
    max-width: 1272px;

    &__wrapper {
        padding: 0 25px 0 275px;    
        position: relative;
    }

    &__pic {
        position: absolute;
        left: 25px;
        top: 0;
    }

    &__header {
        font-family: 'Roboto Condensed';
        font-style: normal;
        font-weight: 400;
        font-size: 36px;
        line-height: 51px;

        color: $color-black;
        margin: 0;
    }

    &__paragraph {
        margin-top: 0;
        margin-bottom: 22px;
    }

    &__text {
        font-family: 'PT Mono', 'Rockwell', serif;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;

        color: $color-black;
        margin-top: 6px;
        margin-bottom: 40px;
    }

    &__button {
        background: none;
        border: 1px solid $color-blue;
        color: $color-blue;

        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.05em;
    }

    &__button-text {
        padding: 12px 50px;
    }
}

@include vp-1023  {
    .about {
        margin-bottom: 80px;

        &__wrapper {            
            padding: 0 50px 0 225px; 
        }

        &__pic {
            left: 46px;
        }

        &__button {
            font-size: 14px;
            line-height: 16px;
        }
        &__text {
            margin-bottom: 0;
        }

    }
}

@include vp-769 {
    .about {

        &__pic {
            display: none;
        }
    }
}