.sections {
    margin-left: auto;
    min-width: 500px; 

    &__list {
        margin: 0;
        display: flex;
        flex-direction: column;
        row-gap: 10px;
        flex-wrap: wrap;
        height: 130px;
    }

    &__item {
        width: 220px;
        line-height: 19px;
        margin-right: 30px;
    }

    &__link {        
        font-family: 'PT Mono', 'Rockwell', serif;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        color: rgba(255, 255, 255, 0.7);
    }
}

@include vp-1023 {
    .sections {
        margin: 0;
        min-width: 0;

        &__list {
            height: auto;
        }
    }
}

@include vp-769 {
    .sections {
        padding: 20px 25px;
        border-top: 1px solid rgba(248, 253, 255, 0.7);
        position: relative;
        
        &__list {
            margin-top: 15px;
        }

        &__header--plus,
        &__header--minus {
            position: absolute;
            right: 26px;
            top: 28px;
            width: 15px;
            height: 0.94px;
            background-color: #C4C4C4;
            cursor: pointer;
        }
        &__header--plus {
            transform: rotate(90deg);
        }
    }
}